import React from 'react'
import './ArtShow.scss'

import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/swiper.min.css"

import HotArea from '../../components/HotArea/HotArea'

class ArtShow extends React.Component{
    constructor(props) {
        super(props);

        this.state = {
            direction: `vertical`,
            list: [
                `http://qn.media.epub360.com/materials/origin/dca5a47854b15651985254a999b0ea43_origin.jpg`,
                `http://qn.media.epub360.com/materials/origin/6d9ded344f59c588d44eeffb7d060c5b_origin.png`
            ],
            imgUrl: null

        }

        this.hidePic = this.hidePic.bind(this)


    }

    showPic(e){
        console.log(e,this.state.list[e])
        this.setState({
            imgUrl: this.state.list[e]
        })
    }
    hidePic(){
        this.setState({
            imgUrl: null
        })
    }

    componentDidMount(){

    }

    componentDidUpdate() {

    }


    render() {
        return <div className={'art-show'}>
            <img ref={this.imgRef} className='bg-img' src="http://qn.media.epub360.com/materials/origin/694ca9209c339e6c31ce7e205fe02c64_origin.jpg" alt=""/>
            {
                this.state.list.map((item,index)=>{
                    return (
                        <div key={index} className={`hot hot-${index}`} onClick={()=>{this.showPic(index)}}></div>
                    )
                })
            }
            {
                this.state.imgUrl?(
                    <img src={this.state.imgUrl} alt="" className="pop-img" onClick={this.hidePic}/>
                ):null

            }

        </div>
    }

}

export default ArtShow;